nav ul {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: min(3rem, 2.5vw);

  list-style: none;

  @media screen and (min-width: 650px) {
    justify-content: flex-end;
  }
}

nav a,
nav a:visited,
nav a:active {
  font-style: none;
  text-decoration: none;
  font-size: 1.25rem;

  margin: 0px 10px;
  padding: 10px 10px;
  position: relative;
  z-index: 0;
  cursor: pointer;

  color: var(--clr-neutral-100);

  &:before,
  &:after {
    position: absolute;
    left: 0px;
    width: 100%;
    height: 2px;
    background: var(--clr-neutral-100);
    content: "";
    opacity: 0;
    transition: all 0.3s;
  }

  &:before {
    top: 0px;
    transform: translateY(10px);
  }

  &:after {
    bottom: 0px;
    transform: translateY(-10px);
  }

  &:hover:before,
  &:hover:after {
    opacity: 1;
    transform: translateY(0px);
  }
}

.cart-quantity {
  padding: 0.3rem 0.5rem;
  margin-left: 0.1rem;
  aspect-ratio: 1/1;
  background: var(--clr-blue);
  color: var(--clr-neutral-100);
  border-radius: 50%;
  font-weight: bold;
}
